<template>
  <Todolist
    :list="todos"
    @deleteTodo="deleteTodo"
    @addTodo="addTodo"
    :totalPage="totalPage"
    @getTodos="getTodos"
    @movePage="movePage"
    @checkTodo="checkTodo"
    @selectLimit="selectLimit"
  />
</template>

<script>
import axios from "axios";
import Todolist from "../components/Todolist";

export default {
  components: { Todolist },
  mounted() {
    this.getTodos();
  },
  data() {
    return {
      todos: [],
      page: 1,
      limit: 10,
      totalPage: 0,
      totalCount: 0,
    };
  },
  methods: {
    async getTodos(searchText) {
      let responseData;
      if (!searchText) {
        responseData = await axios.get(
          `http://localhost:3000/todos?_sort=id&_order=desc&_page=${this.page}&_limit=${this.limit}`
        );
      } else {
        responseData = await axios.get(
          `http://localhost:3000/todos?_sort=id&_order=desc&text_like=${searchText}&_page=${this.page}&_limit=${this.limit}`
        );
      }
      //axios: GET, POST  RESTful API   
      //async, await:             
      // :    (  )      

      this.todos = responseData.data;
      this.totalCount = responseData.headers["x-total-count"];
      const totalPage = () => {
        if (this.totalCount % this.limit != 0) {
          this.totalPage = parseInt(this.totalCount / this.limit) + 1;
        } else if (this.totalCount % this.limit === 0) {
          this.totalPage = parseInt(this.totalCount / this.limit);
        }
      };
      totalPage();
      this.todos = responseData.data;
      //this.listArray = responseData.data;
    },
    async addTodo(todoText) {
      // 
      if (!todoText) return;

      const todoObj = {
        text: todoText,
        isChecked: false,
      };
      //todoText: 
      //text key, this.todoText value
      await axios.post(`http://localhost:3000/todos`, todoObj);
      this.getTodos();
    },
    async deleteTodo(id) {
      await axios.delete(`http://localhost:3000/todos/${id}`);
      if (this.totalCount % this.limit === 1) {
        this.page = this.totalPage - 1;
      }
      this.getTodos();
    },
    /*async changeCount(pageNum, count) {
      const req = await axios.get(
        `http://localhost:3000/todos?_page=${pageNum}&_limit=${count}`
      );
      console.log(req.data);
      this.todos = req.data;
    },*/
    movePage(page) {
      this.page = page;
      this.getTodos();
    },
    async checkTodo(todo) {
      if (!todo) return;
      await axios.patch(`http://localhost:3000/todos/${todo.id}`);
    },
    selectLimit(selectedNum) {
      this.limit = selectedNum;
      this.getTodos();
    },
  },
};
</script>

<style lang="scss" scoped>
input,
button,
select:focus {
  outline: none;
}
</style>
